@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap");
nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: none;
      -ms-flex-pack: none;
          justify-content: none;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  max-width: 90vw;
  margin: 0 auto;
  padding: 25px 10px;
  font-family: "Lato", sans-serif;
}

nav a {
  color: #000;
  text-decoration: none;
  font-size: 1.2rem;
  margin-left: 80px;
}

nav a.active {
  border-bottom: 3px solid #ff0000;
}

.hero {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 80vh;
  max-width: 90vw;
  margin: 0 auto;
  padding: 25px 10px;
}

.hero img {
  position: absolute;
  right: 60px;
  top: 0px;
  width: 630px;
  height: 800px;
  margin-top: -30px;
